@import '../shared/globals.scss';

.typography {
  * {
    margin-bottom: 0;
  }
  h1 {
    font-size: $base * 2;
    line-height: $base * 2;
    margin-top: $base * 4;
    font-weight: normal;
  }
  p {
    line-height: 1.5;
    margin: $base * 1.5 0;

    code {
      background-color: $color-code-background;
      border-radius: 3px;
      margin: 0;
      padding: 0.2em 0.4em;
      font-size: 0.85em;
    }
  }
  strong {
    font-weight: normal;
    text-decoration: underline;
  }
  table {
    border-spacing: 0;
  }
  thead,
  tbody,
  tr {
    padding: 0;
    border: 0;
    margin: 0;
  }
  td,
  th {
    vertical-align: top;
    font-weight: normal;
    text-align: left;
    line-height: $base * 1.5;
    padding: 0 $base * 2 $base * 1.5 0;
    white-space: nowrap;
    @include from('md') {
      white-space: normal;
      &.nowrap {
        white-space: nowrap;
      }
    }
  }
  abbr[title]{
    text-decoration: none;
  }
}
